#menu .overlay {
  padding: 100px 0;
  margin: 0 -15px;
  background: rgba(0, 0, 0, 0.4); }

#menu .nav-tabs {
  margin-bottom: 80px;
  text-align: center;
  border: none; }

#menu .nav-tabs .nav-link {
  font-weight: 400;
  text-transform: uppercase;
  color: #151515;
  border: 2px solid transparent;
  transition: 0.3s; }

#menu .nav-tabs .nav-link.active,
#menu .nav-tabs .nav-link:hover {
  border-bottom: 2px solid #ccad52; }

#menu .menu-list {
  margin: 70px 0; }

#menu .menu-item {
  margin: 45px 0;
  font-size: 18px; }

#menu .menu-item-name {
  font-weight: 400;
  font-size: 1.125rem;
  color: #555555;
  padding-bottom: 15px;
  border-bottom: 2px dotted rgba(0, 0, 0, 0.15); }

#menu .menu-item-price {
  float: right;
  font-weight: 400;
  color: #555555;
  margin-top: -40px; }

#menu .menu-item-description {
  padding-top: 10px;
  font-style: italic;
  font-size: 0.9375rem; }

#menu h2 {
  color: #dddddd; }

#menu p {
  color: rgba(255, 255, 255, 0.75);
  padding: 0 20px; }

#team {
  padding: 120px 0 80px 0; }

#team .card .card-body img {
  min-height: 503px;
  margin-bottom: 20px; }

#team .card .card-body h2 {
  margin-bottom: 10px; }

#team h2 + p {
  margin-bottom: 60px; }

#team .card {
  border: none; }

#reservation {
  background: #444444 url(../img/reservation-bg.jpg) bottom center no-repeat fixed;
  background-size: cover; }

#reservation .overlay {
  font-size: 42px;
  font-weight: 300;
  padding: 100px 0;
  margin: 0 -15px;
  background: rgba(0, 0, 0, 0.4); }

#reservation span {
  color: rgba(255, 255, 255, 0.75);
  padding: 0 20px;
  margin: 0 0 20px 0; }

#reservation span + a {
  color: #ccad52;
  text-decoration: none;
  padding: 0 20px; }

#contact {
  padding-bottom: 60px;
  background: #f6f6f6; }

#contact .contacts-container {
  padding: 50px 0 0 0; }

#contact p {
  font-size: 0.9375rem; }

#contact .contact-item {
  margin-top: 40px; }

#contact .contact-item a {
  color: inherit; }

#contact .contact-item a:hover {
  color: #ccad52; }

#gallery {
  padding: 120px 0;
  background: #222222; }

#gallery h2 {
  color: #ffffff; }

#gallery p {
  font-size: 1.125rem;
  color: #888888;
  margin-top: 50px;
  margin-bottom: 20px; }

#gallery .grid-item {
  position: relative;
  overflow: hidden; }

#gallery .grid-sizer,
#gallery .grid-item {
  width: 100%; }

@media (min-width: 576px) {
  #gallery .grid-sizer,
  #gallery .grid-item {
    width: 50%; } }

@media (min-width: 768px) {
  #gallery .grid-sizer,
  #gallery .grid-item {
    width: 33.3333%; } }

@media (min-width: 992px) {
  #gallery .grid-sizer,
  #gallery .grid-item {
    width: 25%; } }

#gallery .grid .grid-item img {
  cursor: pointer;
  transition: all 0.5s ease-out; }

#gallery .grid .grid-item img:hover {
  transform: scale(1.1); }

#image-gallery .modal-dialog {
  width: auto;
  height: 90%; }

#map {
  height: 100%;
  min-height: 500px; }

footer {
  padding-top: 50px;
  padding-bottom: 20px;
  background: #262626; }

footer .social-list {
  list-style: none;
  display: flex; }

footer .social-list li {
  margin: 0 15px; }

footer .social-list li i.fa {
  cursor: pointer;
  font-size: 1.625rem;
  padding: 4px;
  color: #ccad52;
  transition: all 0.3s; }

footer .copyrights {
  margin-top: 15px; }

footer .social-list li i.fa:hover {
  color: #ffffff; }

.button-group {
  padding: 15px 15px; }

.btn-link {
  border: none;
  cursor: pointer;
  color: #888888;
  font-size: 1rem;
  font-weight: 400;
  padding: 15px 15px;
  border-radius: 0;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none; }

.btn-link.is-checked,
.btn-link:hover {
  color: #ccad52;
  outline: none; }

/*---To Top button---*/
.totop-button {
  position: fixed;
  right: 30px;
  bottom: 30px;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 20px solid #c6c6c6;
  z-index: 1000;
  transition: .3s;
  border-radius: 3px;
  opacity: 0;
  visibility: hidden; }

.totop-button:hover {
  cursor: pointer;
  border-bottom-color: #111111; }

.totop-button.show {
  opacity: 1;
  visibility: visible; }

